<script lang="js">
	import { LottiePlayer } from '@lottiefiles/svelte-lottie-player';
	import { MotionDiv } from 'svelte-motion';

	const lottieFiles = [
		"https://assets8.lottiefiles.com/packages/lf20_ygtui7kv.json", 
		"https://assets3.lottiefiles.com/packages/lf20_q9sek5kq.json"
	]
	let i = 0;
	let lottieSrc = lottieFiles[i];
    let variant = "gone";

    variant = "enter";
    setTimeout(() => {
        variant = "exit";
    }, 4000);

	setInterval(() => {
        i = (i + 1) % 2;
        lottieSrc = lottieFiles[i];
        variant = "enter";
        setTimeout(() => {
            variant = "exit";
        }, 4000);
	}, 5000);

	const variants = {
		gone: {
			opacity: 0,
			transition: {
				duration: 0
			}
		},
		enter: {
			x: [500, 0],
			opacity: [0, 1],
			transition: {
				duration: 1
			}
		},
		exit: {
			x: [0, -500],
			opacity: [1, 0],
			transition: {
				duration: 1
			}
		}
	};
</script>

<MotionDiv variants={variants} animate={variant}>
    <LottiePlayer
        src={lottieSrc}
        autoplay="{true}"
        loop="{false}"
        renderer="svg"
        background="transparent"
        height="500"
        width="500"
    />
</MotionDiv>